import React from 'react';
import { platform, IOS, PanelHeaderButton, Card, CardGrid, Cell, List, Panel, PanelHeader, Group } from '@vkontakte/vkui';

//иконки
import Icon28BugOutline from '@vkontakte/icons/dist/28/bug_outline'; //коронавирус
import Icon28MoneyTransferOutline from '@vkontakte/icons/dist/28/money_transfer_outline'; //цифровой пропуск
import Icon28Users3Outline from '@vkontakte/icons/dist/28/users_3_outline'; //волонтеры
import Icon28HomeOutline from '@vkontakte/icons/dist/28/home_outline'; //режим самоизоляции

//иконки назад
import Icon28ArrowLeftOutline from '@vkontakte/icons/dist/28/arrow_left_outline'; //iso
import Icon24Back from '@vkontakte/icons/dist/24/back'; //android

const osname = platform();

const Covidmenu = props => (
    <Panel id={props.id}>
        <PanelHeader
        left={<PanelHeaderButton onClick={props.go} data-to="home">
        {osname === IOS ? <Icon28ArrowLeftOutline/> : <Icon24Back/>}
    </PanelHeaderButton>}
        >
         Меню
        </PanelHeader>
        <Group>
        <CardGrid>
          <Card size="l" mode="shadow">
          <List>
        <Cell expandable before={<Icon28BugOutline />} onClick={props.go} data-to="covid">Коронавирус в области</Cell>
        <Cell expandable before={<Icon28MoneyTransferOutline />} onClick={props.go} data-to="pass">Цифровые пропуска</Cell>
        <Cell expandable before={<Icon28Users3Outline />} onClick={props.go} data-to="volunteers">Волонтёры города</Cell>
        <Cell expandable before={<Icon28HomeOutline />} onClick={props.go} data-to="stayhome">Режим самоизоляции</Cell>
        </List>
            <div style={{ height: 20 }} />
          </Card>
        </CardGrid>
    </Group>

    </Panel>
);

export default Covidmenu;