import React from 'react';
import { Card, CardGrid, Cell, List, Panel, PanelHeader, Group } from '@vkontakte/vkui';

//иконки
import Icon28BugOutline from '@vkontakte/icons/dist/28/bug_outline'; // Коронавирус
import Icon28ArticleOutline from '@vkontakte/icons/dist/28/article_outline'; // О приложении
import Icon28SettingsOutline from '@vkontakte/icons/dist/28/settings_outline'; // Настройки
import Icon28TargetOutline from '@vkontakte/icons/dist/28/target_outline'; // Организации

const Home = props => (
    <Panel id={props.id}>
        <PanelHeader>Прайд</PanelHeader>
        <Group>
        <CardGrid>
          <Card size="l" mode="shadow">
          <List>
        <Cell expandable before={<Icon28BugOutline />} onClick={props.go} data-to="covidmenu">Коронавирус</Cell>
        <Cell expandable before={<Icon28ArticleOutline />} onClick={props.go} data-to="info">О приложении</Cell>
        <Cell expandable before={<Icon28TargetOutline />}>Организации города (#404)</Cell>
        <Cell expandable before={<Icon28SettingsOutline />}>Настройки (#404)</Cell>
        </List>
            <div style={{ height: 20 }} />
          </Card>
        </CardGrid>


    </Group>

    </Panel>
);

export default Home;