import React from 'react';
import { View } from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';
import Home from './panels/Home'
import Info from './panels/Info'
import Covid from './panels/Covid'
import Covidmenu from './panels/Covidmenu'
import Pass from './panels/Pass'
import Volunteers from './panels/Volunteers'
import Stayhome from './panels/Stayhome'

import bridge from '@vkontakte/vk-bridge'
bridge.send('VKWebAppInit');

class App extends React.Component {
    constructor(props) {
        super(props)
        this.state={
            ActivePanel: "home"

        }
    }

    go = e => {
        this.setState({ActivePanel: e.currentTarget.dataset.to})
    }

    render() {
        return (
           <View activePanel={this.state.ActivePanel} >
           <Home id="home" go={this.go} />
           <Info id="info" go={this.go} />
           <Covid id="covid" go={this.go} />
           <Covidmenu id="covidmenu" go={this.go} />
           <Pass id="pass" go={this.go} />
           <Volunteers id="volunteers" go={this.go} />
           <Stayhome id="stayhome" go={this.go} />
           </View>
            
        )
    }
}

export default App;