import React from 'react';
import {  Div, Header, Counter, Card, CardGrid, platform, List, IOS, Cell, Panel, Group, PanelHeader, PanelHeaderButton } from '@vkontakte/vkui';

// иконки
import Icon28UserAddOutline from '@vkontakte/icons/dist/28/user_add_outline'; //выздоровело
import Icon28Users from '@vkontakte/icons/dist/28/users'; //заражено
import Icon28User from '@vkontakte/icons/dist/28/user'; //умерло
import Icon28HistoryForwardOutline from '@vkontakte/icons/dist/28/history_forward_outline'; //обновление

//иконки назад
import Icon28ArrowLeftOutline from '@vkontakte/icons/dist/28/arrow_left_outline'; //ios
import Icon24Back from '@vkontakte/icons/dist/24/back'; //android

const osname = platform();

const Covid = props => (
    <Panel id={props.id}>
        <PanelHeader
        left={<PanelHeaderButton onClick={props.go} data-to="covidmenu">
        {osname === IOS ? <Icon28ArrowLeftOutline/> : <Icon24Back/>}
    </PanelHeaderButton>}
        >
            Коронавирус
        </PanelHeader>
        <Group> 
        <CardGrid>
          <Card size="l" mode="shadow">
          <List>
       <Cell before={<Icon28UserAddOutline />} indicator={<Counter mode="primary">3</Counter>}>Выздоровело:</Cell>
       <Cell before={<Icon28Users />} indicator={<Counter mode="primary">8</Counter>}>Заражено:</Cell>
       <Cell before={<Icon28User />} indicator={<Counter>0</Counter>}>Умерло:</Cell>
       <Cell before={<Icon28HistoryForwardOutline />} indicator={<Counter mode="primary">14:05</Counter>}>Последнее обновление:</Cell>
     </List>
            <div style={{ height: 10 }} />
          </Card>
        </CardGrid>
        <CardGrid>
          <Card size="l" mode="outline">
          {<Header mode="secondary">Дополнительно</Header>}
          <Div style={{ paddingTop: 60, paddingBottom: 60, color: 'black' }}>
            Что означает данная информация?
            <br/>
            <br/>
            Первое поле: количество выписанных за весь период людей.
            <br/>
            <br/>
            Второе поле: количество людей, которые находятся на лечении.
            <br/>
            <br/>
            Третье поле: количество смертей за весь период.
            <br/>
            <br/>
            Четвёртое поле: время последнего обновления информации.
            </Div>
            <div style={{ height: 30 }} />
          </Card>
        </CardGrid>
    </Group>
    </Panel>
);

export default Covid;