import React from 'react';
import { getAvatarUrl, Header, Avatar, Cell, Card, CardGrid, Group, Div, Panel, PanelHeader, PanelHeaderButton, platform, IOS } from '@vkontakte/vkui';

//иконки назад
import Icon28ArrowLeftOutline from '@vkontakte/icons/dist/28/arrow_left_outline'; //iso
import Icon24Back from '@vkontakte/icons/dist/24/back'; //android

import Icon28LogoVkOutline from '@vkontakte/icons/dist/28/logo_vk_outline';

const osname = platform();

const Info = props => (
    <Panel id={props.id}>
        <PanelHeader
        left={<PanelHeaderButton onClick={props.go} data-to="home">
        {osname === IOS ? <Icon28ArrowLeftOutline/> : <Icon24Back/>}
    </PanelHeaderButton>}
        >
            О приложении
        </PanelHeader>
        <Group>
        <CardGrid>
          <Card size="l" mode="shadow">
          <Div style={{ paddingTop: 60, paddingBottom: 60, color: 'gray' }}>
            Приложение 'Прайд', первое приложение на платформе ВКонтакте в нашем городе Далматово.
            <br/>
            Уважаемые Пользователи! Сообщаем вам, что приложение находится в стадии разработки, но как вы заметили, оно доступно уже сейчас. Обо всех ошибках сообщайте разработчику. Ссылка ниже. Также, вы могли заметить кнопку в виде ключа. Нажав на кнопку вы можете посмотреть код элемента приложения. Eruda будет доступна неделю, пока идут тестирования.
            <br/>
            Приложение работает без разрешения на доступ к информации о вашем аккаунте, а значит, вы можете не бояться использовать его. Приложение проходит обновление с 10:00 до 13:00 по МСК. Если требуется, обновление может пройти в любое время.
          </Div>
            <div style={{ height: 20 }} />
            {<Header mode="secondary">Разработчик</Header>}
            <Cell before={<Avatar><Icon28LogoVkOutline /></Avatar>}>Егор Гордеев</Cell>
          </Card>
        </CardGrid>
        </Group>
    </Panel>
);

export default Info;