import React from 'react';
import {  Div, FixedLayout, Card, CardGrid, platform, IOS, Panel, Group, PanelHeader, PanelHeaderButton } from '@vkontakte/vkui';

// иконки


//иконки назад
import Icon28ArrowLeftOutline from '@vkontakte/icons/dist/28/arrow_left_outline'; //iso
import Icon24Back from '@vkontakte/icons/dist/24/back'; //android

const osname = platform();

const Volunteers = props => (
    <Panel id={props.id}>
        <PanelHeader
        left={<PanelHeaderButton onClick={props.go} data-to="covidmenu">
        {osname === IOS ? <Icon28ArrowLeftOutline/> : <Icon24Back/>}
    </PanelHeaderButton>}
        >
         Волонтёры
        </PanelHeader>
        <Group>
        <FixedLayout vertical="top">
          </FixedLayout>
                  <CardGrid>
          <Card size="l" mode="shadow">
          <Div style={{ paddingTop: 60, paddingBottom: 60, color: 'gray' }}>
          Районный волонтерский штаб для оказания помощи одиноким пенсионерами на время карантина начал свою активную деятельность.
            <br/>
            <br/>
            Как известно, лучший способ профилактики - избегать людных мест, свести к минимуму походы в магазин. Наиболее уязвимы перед угрозой распространения коронавирусной инфекции люди преклонного возраста. Их и постараются оградить от опасностей волонтеры. Их работа нацелена на оказание помощи данной категории граждан в покупке продуктов, медикаментов и выносе мусора.
            <br/>
            <br/>
            Об этом сегодня и рассказали волонтеры одиноко проживающим пенсионерам города Далматово. Посетив каждого на дому, они сообщили им о карантине, о графике посещения торговых объектов и раздали памятки с номером телефона круглосуточной единой горячей линии для оказания помощи пенсионерам в период сложной эпидемиологической ситуации с коронавирусом.
            <br/>
            <br/>
            Руководитель Штаба отмечает:
            <br/>
            <br/>
            – Для приема звонков создана федеральная горячая линия 8-800-200-34-11. По этому телефону может обратиться любой человек из данной категории и попросить оказать ему помощь. Главное, чтобы человек из группы риска как можно меньше выходил из дома на время карантина. Телефон горячей линии открыт и в Далматовском районе. Отмечу, что контакт волонтеров с пожилыми людьми, нуждающимися в помощи, при выполнении заявок будет максимально ограничен. На сегодняшний день оказана одна услуга - приобретены лекарства для пенсионерки.
            <br/>
            <br/>
            Всего волонтеры посетили около 60 граждан. Работа продолжается.
Напомним, что воспользоваться услугами волонтеров граждане пожилого возраста могут, сделав предварительную заявку, позвонив по телефонам:
- круглосуточная единая горячая линия для оказания помощи пенсионерам в период сложной эпидемиологической ситуации с коронавирусом - 8 (800) 200 3411
            <br/>
            <br/>
            - районный телефон горячей линии – 89226730480.
            <br/>
            <br/>
            - районный координатор Штаба волонтеров – 89323139989.
          </Div>
          <div style={{ height: 20 }} />
          </Card>
        </CardGrid>
    </Group>
    </Panel>
);

export default Volunteers;