import React from 'react';
import {  Div, FixedLayout, Card, CardGrid, platform, IOS, Panel, Group, PanelHeader, PanelHeaderButton } from '@vkontakte/vkui';


//иконки назад
import Icon28ArrowLeftOutline from '@vkontakte/icons/dist/28/arrow_left_outline'; //iso
import Icon24Back from '@vkontakte/icons/dist/24/back'; //android

const osname = platform();

const Pass = props => (
    <Panel id={props.id}>
        <PanelHeader
        left={<PanelHeaderButton onClick={props.go} data-to="covidmenu">
        {osname === IOS ? <Icon28ArrowLeftOutline/> : <Icon24Back/>}
    </PanelHeaderButton>}
        >
         Пропуск
        </PanelHeader>
        <Group>
        <FixedLayout vertical="top">
          </FixedLayout>
                  <CardGrid>
          <Card size="l" mode="outline">
          <Div style={{ paddingTop: 60, paddingBottom: 60, color: 'black' }}>
Как работает цифровой пропуск на период самоизоляции для жителей Курганской области.
            <br/>
            <br/>
            Когда вам потребуется выйти из дома, запросите пропуск через СМС.
            <br/>
            <br/>
            Отправьте на номер +79037672668 сообщение вида [Серия и номер паспорта (без пробелов)] [пробел] [номер цели].
            <br/>
            <br/>
            Если вы гражданин другой страны, в качестве документа используйте вид на жительство, миграционную карту или визу.
            <br/>
            <br/>
            Выберите цель из списка:
            <br/>
            <br/>
            1.️ Получение государственных и муниципальных услуг.
            <br/>
            <br/>
            2. ️Сопровождение несовершеннолетнего.
            <br/>
            <br/>
            3. Посещение медицинской или ветеринарной организации.
            <br/>
            <br/>
            4. Участие в похоронах.
            <br/>
            <br/>
            5. Выезд на дачу (в загородный дом) и возвращение с дачи (из загородного дома).
            <br/>
            <br/>
            6. Cледования к месту приобретения товаров, работ, услуг, реализация которых не ограничена.
            <br/>
            <br/>
            7. Посещение кредитных организаций, отделений почтовой связи.
            <br/>
            <br/>
            8. Доставка лекарств, продуктов питания и предметов первой необходимости нетрудоспособным гражданам.
            <br/>
            <br/>
            9. Посещение религиозных объектов.
            <br/>
            <br/>
            Пример СМС:
            <br/>
            1234567890 1
            <br/>
            Как работает система?
            <br/>
            <br/>
            Сотрудники контролирующих органов на улице или в общественном месте могут запросить подтверждение на право выхода из дома. В случае если гражданин покинул место самоизоляции безосновательно, он будет привлечен к административной ответственности.
            <br/>
            <br/>
            Допускается не больше 2 выходов в сутки продолжительностью до 1 часа.
            <br/>
            <br/>
            Отнеситесь с пониманием к вводимым мерам: система пропусков позволяет контролировать посещение общественных мест, предотвращает скопление людей. Вы сможете сделать необходимые дела с минимальным риском для себя и окружающих. Будьте здоровы!
          </Div>
          <div style={{ height: 20 }} />
          </Card>
        </CardGrid>
    </Group>
    </Panel>
);

export default Pass;